/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
    font-family: 'PT mono', monospace;
    font-size: 1em;
    line-height: 1.4;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

